body
	margin: 0
	padding: 0
	-webkit-font-smoothing: antialiased
	font-style: normal 
	font-family: Archivo 
	
@import responsive
*
	box-sizing: border-box
	outline: none

input, button, input:active, button:active
	outline: none
input
	-webkit-appearance: none 


ul
	list-style-type: none
	padding: 0
	margin: 0

.clear
	clear: both

a:focus
	outline: none


.hidden-item
	display: none

.visible-item
	display: block


h1, h2, h3
	margin-top: 0
	margin-bottom: 0

p
	margin-top: 0
	margin-bottom: 0

a
	text-decoration: none

.img-responsive
	display: block
	max-width: 100%

.s-wrapper
	overflow: hidden
.s-wrap
	margin: 0 auto
	padding: 0 20px
	max-width: 1320px
	width: 100%
@include respond-to(tablet)

.flex
	display: -webkit-flex
	display: -moz-flex 
	display: -ms-flex 
	display: -o-flex 
	display: flex 
	justify-content: flex-start 
	align-items: flex-start 
	align-content: stretch 
	flex-wrap: wrap 
.flexbetween
	justify-content: space-between
.flexcenter
	justify-content: center
.flexright
	justify-content: flex-end
.flexalcenter
	align-items: center
.flexalbottom
	align-items: flex-end
.flexalstretch
	align-items: stretch
.flexalccenter
	align-content: center
.flexnwr
	flex-wrap: nowrap
.inflex
	display: inline-flex

.s-header
	padding: 27px 0 23px
	border-bottom: 1px solid rgba(255, 255, 255, 0.15)  
	&.dark
		padding: 19px 0 21px 
		border-bottom: 1px solid rgba(0, 0, 0, 0.15)  
		.s-header-top
			background: var(--White-950, #151515)  
		.s-header-menu
			li
				a
					color: #151515  
					&:hover
						color: #FF4D00
	&-top
		display: none
		padding: 15px 0 13px
		border-bottom: 1px solid rgba(255, 255, 255, 0.15)  
	&-contacts
		gap: 26px
		a
			color: var(--White-50, #FFF) 
			font-size: 14px 
			font-weight: 400 
			line-height: 140% 
			display: flex
			align-items: center
			gap: 7px
	&-soc
		margin-left: auto
		gap: 10px
	&-nav
		display: none
	&-toggle
		margin-left: auto
	&-btn
		background: var(--Orange-600, #FF4D00)  
		color: var(--White-50, #FFF) 
		font-size: 14px 
		font-weight: 600 
		line-height: normal 
		letter-spacing: 0.7px 
		text-transform: uppercase  
		display: flex
		align-items: center
		justify-content: center
		gap: 10px
		width: 185px
		height: 48px
		transition: .3s all
		&.white
			background: #FFF  
			color: #000  
			svg
				path
					transition: .3s all
			&:hover, &:active
				background: #000
				color: #fff
				svg
					path
						fill: #fff
		&.border
			border: 1px solid var(--Orange-600, #FF4D00)  
			background: transparent
			color: var(--Orange-600, #FF4D00)  
			svg
				path
					transition: .3s all
			&:hover, &:active
				color: #fff
				background: #FF4D00
				svg
					path
						fill: #fff
		&:hover, &:active
			background-color: #DF2D00
	&-menu
		li
			a
				color: var(--White-100, #EFEFEF) 
				font-size: 14px 
				font-weight: 500 
				line-height: 140%  /* 19.6px */
				transition: .3s all
				text-transform: uppercase  
				&:hover
					color: #FF4D00
	&-toggle
		cursor: pointer
		img
			pointer-events: none


